/*スクロールダウン全体の場所*/
.c-animobj__scrolldown {
  position: absolute;
  /* 丸の描写 */
  /*下からの距離が変化して丸の全体が上から下に動く*/
  /*上から下にかけて丸が透過→不透明→透過する*/
  /* 線の描写 */
}
.c-animobj__scrolldown span {
  /*描画位置*/
  position: absolute;
  left: 10px;
  bottom: 10px;
  color: #391E88;
  font-size: 12px;
  line-height: 1;
  letter-spacing: 0.2em;
  /*縦書き設定*/
  -ms-writing-mode: tb-rl;
  -webkit-writing-mode: vertical-rl;
  writing-mode: vertical-rl;
}
.c-animobj__scrolldown.--vertical span {
  color: #FFFFFF;
  position: relative;
  left: -45%;
  top: -80px;
  bottom: 0;
}
.c-animobj__scrolldown:before {
  content: "";
  /*描画位置*/
  position: absolute;
  bottom: 0;
  left: -4px;
  /*丸の形状*/
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #FFFFFF;
  /*丸の動き1.6秒かけて透過し、永遠にループ*/
  animation: circlemove 1.6s ease-in-out infinite, cirlemovehide 1.6s ease-out infinite;
}
@keyframes circlemove {
  0% {
    bottom: 75px;
  }
  100% {
    bottom: -5px;
  }
}
@keyframes cirlemovehide {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  80% {
    opacity: 0.9;
  }
  100% {
    opacity: 0;
  }
}
.c-animobj__scrolldown:after {
  content: "";
  /*描画位置*/
  position: absolute;
  bottom: 0;
  left: 0;
  /*線の形状*/
  width: 2px;
  height: 80px;
  background: #FFFFFF;
}

/*  map-get(マップ型変数,キー)で値を取得
@media screen and (max-width: 767px) {
 */
@media screen and (max-width: 767px) {
  /*  呼び出しもとの値が入る */
  .c-animobj__scrolldown {
    /*下からの距離が変化して丸の全体が上から下に動く*/
    /* 線の描写 */
  }
  .c-animobj__scrolldown.--vertical span {
    top: -50px;
  }
  @keyframes circlemove {
    0% {
      bottom: 45px;
    }
  }
  .c-animobj__scrolldown:after {
    height: 50px;
  }
}
